import axios, {BASEURL} from '../../utils/axios'
import {getUserId} from "@/utils/util";

/**
 * 当日血糖首页详情
 * @returns {*}
 */
export function getHomeDetial(measureTimeDesc,day) {
  return axios.get(`/monitor/monitor/bmi/SyLogBmiHistory/getBmiDetail`,{ params: {
      measureTimeDesc, day, userId: getUserId()
    }});
}

/**
 * 添加
 * @returns {*}
 */
export function insert(measureTimeDesc,weight,height,waist,day) {
    console.log('添加血压记录',...arguments)
    const params = {
        measureTimeDesc,weight,height,waist,day,userId: getUserId()
    }
    const data = []
    data.push(params)
    return axios.post(`/monitor/monitor/bmi/SyLogBmiHistory/insert`,data);
}

/**
 * 当日数据
 * @param startTime
 * @param endTime
 * @param userId
 * @returns {*}
 */
export function getListByStartEnd(startTime,endTime){
    return axios.get(`/monitor/monitor/bmi/SyLogBmiHistory/getListByStartEnd`,{ params: {
            startTime, endTime, userId: getUserId()
        }});
}


