<template>
  <div class="title-bar">
    <header class="simple-header">
      <p class="left">
        <van-icon name="arrow-left"  size="22" @click="goBack" v-if="!back"/>
      </p>
      <span class="simple-header-name">{{ name }}</span>
      <p class="right">
        <span @click="rightPress" class="txt-right">{{ rightName }}</span>
        <img :src="imgForward" @click="rightPress" v-if="imgForward" class="img-forward" alt="">
      </p>
    </header>
  </div>
</template>

<script>
import {ref} from 'vue'
import {useRouter} from 'vue-router'

export default {
  props: {
    name: {
      type: String,
      default: ''
    },
    back: {
      type: String,
      default: ''
    },
    noback: {
      type: Boolean,
      default: false
    },
    rightName: {
      type: String,
      default: ''
    },
    imgForward: {
      type: String,
      default: '',
    },
    rightPress: {
      type: Function,
      default: () => {
      }
    }
  },
  emits: ['callback'],
  setup(props, ctx) {
    const back = ref(props.noback)
    const router = useRouter()
    const goBack = () => {
      if (!props.back) {
        router.go(-1)
      } else {
        router.push({path: props.back})
      }
      ctx.emit('callback')
    }
    return {
      goBack,
      back,
    }
  }
}
</script>

